/* Header.css */

header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    background-image: linear-gradient(rgb(4, 33, 122), rgba(245, 245, 245, 0.473));
  }
  
  header h1 {
    font-size: 35px;
    color: black;
  }
  
  nav {
    flex: 1;
  }
  
  nav ul {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    gap: 10px;
    margin: 0;
    padding: 0;
  }
  
  nav li {
    margin: 0;
  }
  
  nav li a {
    text-decoration: none;
    color: black;
    font-size: 16px;
  }
  
  nav li a:hover {
    text-decoration: underline;
    
  }
  