/* Contact.css */

section {
    padding: 20px;
  }
  
  h2 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  form {
    display: grid;
    gap: 10px;
  }
  
  label {
    display: grid;
    gap: 5px;
  }
  
  input[type="text"],
  input[type="email"],
  textarea {
    width: 98%;
    padding: 8px; /* Add padding on the top/bottom and left/right */
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  
  textarea {
    height: 220px; /* Adjust the height to your preference */
  }
  
  input[type="submit"] {
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    border-radius: 4px;
    border: none;
    cursor: pointer;
  }
  
  input[type="submit"]:hover {
    background-color: #0056b3;
  }
  /* Contact.css */

