/* Footer.css */

footer {
    background-image: linear-gradient(rgb(4, 33, 122), rgba(245, 245, 245, 0.473));
    padding: 10px;
    display: flex;
    justify-content: center;
  }
  
  footer a {
    margin-right: 10px;
    text-decoration: none;
    color: black;
    font-size: 16px;
    display: flex;
    align-items: center;
  }
  
  footer a:hover {
    text-decoration: underline;
  }
  